@font-face {
  font-family: m-inconsolata;
  src: url(0fbe014c1f0fb5e3c71140ff0dc63edf.otf);
}
body {
  margin: 0;
}
.fin-cell {
  font-size: 13px;
  line-height: normal;
  user-select: none;
  -moz-user-select: none;
  font-family: 'Lato', 'Source Sans Pro', Roboto, Helvetica, Arial, sans-serif;
  box-sizing: content-box;
  background: #fff;
  -webkit-font-smoothing: antialiased;
}
.fin-cell textarea {
  font: 400 12px Arial, 'Lato', 'Source Sans Pro', Roboto, Helvetica, sans-serif;
}
.fin-cell-sheet {
  position: relative;
  overflow: hidden;
}
.fin-cell-table {
  vertical-align: bottom;
}
.fin-cell-history-border {
  width: 175px;
  max-height: 350px;
  overflow-y: auto;
  border: 1px solid transparent;
  border-radius: 4px;
  box-shadow: 0 2px 6px 2px rgba(60, 64, 67, 0.15);
}
.fin-cell-tooltip {
  font-family: inherit;
  position: absolute;
  padding: 5px 10px;
  color: #fff;
  border-radius: 1px;
  background: #000000;
  font-size: 12px;
  z-index: 100000;
}
.fin-cell-tooltip:before {
  pointer-events: none;
  position: absolute;
  left: calc(50% - 4px);
  top: -4px;
  content: "";
  width: 8px;
  height: 8px;
  background: inherit;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  z-index: 1;
  box-shadow: 1px 1px 3px -1px rgba(0, 0, 0, 0.3);
}
.fin-cell-color-palette {
  padding: 5px;
}
.fin-cell-color-palette table {
  margin: 0;
  padding: 0;
  border-collapse: separate;
  border-spacing: 2px;
  background: #fff;
}
.fin-cell-color-palette table td {
  margin: 0;
  cursor: pointer;
  border: 1px solid transparent;
}
.fin-cell-color-palette table td:hover {
  border-color: #ddd;
}
.fin-cell-color-palette table td .fin-cell-color-palette-cell {
  width: 16px;
  height: 16px;
}
.fin-cell-border-palette {
  padding: 6px;
}
.fin-cell-border-palette table {
  margin: 0;
  padding: 0;
  border-collapse: separate;
  border-spacing: 0;
  background: #fff;
  table-layout: fixed;
}
.fin-cell-border-palette table td {
  margin: 0;
}
.fin-cell-border-palette .fin-cell-border-palette-left {
  border-right: 1px solid #eee;
  padding-right: 6px;
}
.fin-cell-border-palette .fin-cell-border-palette-left .fin-cell-border-palette-cell {
  width: 30px;
  height: 30px;
  cursor: pointer;
  text-align: center;
}
.fin-cell-border-palette .fin-cell-border-palette-left .fin-cell-border-palette-cell .fin-cell-icon-img {
  opacity: 0.8;
}
.fin-cell-border-palette .fin-cell-border-palette-left .fin-cell-border-palette-cell:hover {
  background-color: #eee;
}
.fin-cell-border-palette .fin-cell-border-palette-right {
  padding-left: 6px;
}
.fin-cell-border-palette .fin-cell-border-palette-right .fin-cell-toolbar-btn {
  margin-top: 0;
  margin-bottom: 3px;
}
.fin-cell-border-palette .fin-cell-border-palette-right .fin-cell-line-type {
  position: relative;
  left: 0;
  top: -3px;
}
.fin-cell-dropdown {
  position: relative;
}
.fin-cell-dropdown .fin-cell-dropdown-content {
  position: absolute;
  z-index: 200;
  background: #fff;
  box-shadow: 1px 2px 5px 2px rgba(51, 51, 51, 0.15);
}
.fin-cell-dropdown.bottom-left .fin-cell-dropdown-content {
  top: calc(100% + 5px);
  left: 0;
}
.fin-cell-dropdown.bottom-right .fin-cell-dropdown-content {
  top: calc(100% + 5px);
  right: 0;
}
.fin-cell-dropdown .fin-cell-dropdown-title {
  padding: 0 5px;
  display: inline-block;
}
.fin-cell-dropdown .fin-cell-dropdown-header .fin-cell-icon.arrow-left {
  margin-left: 4px;
}
.fin-cell-dropdown .fin-cell-dropdown-header .fin-cell-icon.arrow-right {
  width: 10px;
  margin-right: 4px;
}
.fin-cell-dropdown .fin-cell-dropdown-header .fin-cell-icon.arrow-right .arrow-down {
  left: -130px;
}
.fin-cell-icon.arrow-right_d:hover,
.fin-cell-icon.arrow-right_d.active {
  background: rgba(0, 0, 0, 0.5);
}
/* resizer **/
.fin-cell-resizer {
  position: absolute;
  z-index: 11;
}
.fin-cell-resizer .fin-cell-resizer-hover {
  background-color: rgba(75, 137, 255, 0.25);
}
.fin-cell-resizer .fin-cell-resizer-line {
  position: absolute;
}
.fin-cell-resizer.horizontal {
  cursor: row-resize;
}
.fin-cell-resizer.horizontal .fin-cell-resizer-line {
  border-bottom: 2px dashed #4b89ff;
  left: 0;
  bottom: 0;
}
.fin-cell-resizer.vertical {
  cursor: col-resize;
}
.fin-cell-resizer.vertical .fin-cell-resizer-line {
  border-right: 2px dashed #4b89ff;
  top: 0;
  right: 0;
}
/* scrollbar */
.fin-cell-scrollbar {
  position: absolute;
  bottom: 0;
  right: 0;
  background-color: #f4f5f8;
  opacity: 0.9;
  z-index: 12;
}
.fin-cell-scrollbar.horizontal {
  right: 15px;
  overflow-x: scroll;
  overflow-y: hidden;
}
.fin-cell-scrollbar.horizontal > div {
  height: 1px;
  background: #ddd;
}
.fin-cell-scrollbar.vertical {
  bottom: 15px;
  overflow-x: hidden;
  overflow-y: scroll;
}
.fin-cell-scrollbar.vertical > div {
  width: 1px;
  background: #ddd;
}
.fin-cell-scrollbar:hover {
  opacity: 0.85;
}
/* @{css-prefix}-overlayer */
.fin-cell-overlayer {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 10;
}
.fin-cell-overlayer .fin-cell-overlayer-content {
  position: absolute;
  overflow: hidden;
  pointer-events: none;
  width: 100%;
  height: 100%;
}
.fin-cell-editor,
.fin-cell-selector {
  position: absolute;
  overflow: hidden;
  pointer-events: none;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.aaa {
  padding: 0px;
  height: auto;
  line-height: normal;
}
.fin-cell-selector-box-t {
  position: absolute;
  top: -4px;
  width: 100%;
  height: 5px;
  cursor: move;
  pointer-events: auto;
}
.fin-cell-selector-box-l {
  position: absolute;
  top: -4px;
  width: 5px;
  height: 100%;
  cursor: move;
  pointer-events: auto;
}
.fin-cell-selector-box-r {
  position: absolute;
  top: -4px;
  width: 5px;
  height: 100%;
  right: 0px;
  cursor: move;
  pointer-events: auto;
}
.fin-cell-selector-box-b {
  position: absolute;
  bottom: -4px;
  width: 90%;
  height: 5px;
  cursor: move;
  pointer-events: auto;
}
.fin-cell-selector-box-l-move-l {
  position: absolute;
  bottom: -4px;
  width: 10px;
  height: 10px;
  cursor: sw-resize;
  pointer-events: auto;
  left: -4px;
}
.fin-cell-selector-box-l-move-t {
  position: absolute;
  top: -4px;
  width: 10px;
  height: 10px;
  cursor: nw-resize;
  pointer-events: auto;
  left: -4px;
}
.fin-cell-selector-box-l-move-r {
  position: absolute;
  top: -4px;
  width: 10px;
  height: 10px;
  right: -4px;
  cursor: ne-resize;
  pointer-events: auto;
}
.fin-cell-selector-box-l-move-b {
  position: absolute;
  bottom: -4px;
  width: 10px;
  height: 10px;
  cursor: se-resize;
  pointer-events: auto;
  right: -4px;
}
/* @{css-prefix}-selector */
.fin-cell-selector .fin-cell-selector-area {
  position: absolute;
  border: 1px solid rgba(75, 137, 255);
  background: rgba(75, 137, 255, 0.1);
  cursor: pointer;
}
.fin-cell-selector .fin-cell-selector-boxinner {
  width: 100%;
  height: 100%;
}
.fin-cell-selector .fin-cell-selector-box {
  bottom: 0;
  right: 0;
  position: absolute;
  cursor: default;
  width: 90%;
  height: 85%;
  margin: auto;
  left: 0;
  top: 0;
}
.fin-cell-selector .fin-cell-selector-clipboard,
.fin-cell-selector .fin-cell-selector-autofill {
  position: absolute;
  background: transparent;
  z-index: 100;
}
.fin-cell-selector .fin-cell-selector-clipboard {
  border: 2px dashed #4b89ff;
}
.fin-cell-selector .fin-cell-selector-autofill {
  border: 1px dashed rgba(0, 0, 0, 0.45);
}
.fin-cell-selector .fin-cell-selector-corner {
  pointer-events: auto;
  position: absolute;
  cursor: crosshair;
  font-size: 0;
  height: 5px;
  width: 5px;
  right: -5px;
  bottom: -5px;
  border: 2px solid #ffffff;
  background: #4b89ff;
}
.fin-cell-editor {
  z-index: 20;
}
.fin-cell-editor .fin-cell-editor-area {
  position: absolute;
  text-align: left;
  line-height: 0;
  z-index: 100;
  pointer-events: auto;
}
.fin-cell-editor .fin-cell-editor-area textarea {
  box-sizing: content-box;
  border: none;
  padding: 0 3px;
  outline-width: 0;
  resize: none;
  text-align: start;
  overflow-y: hidden;
  font: 400 12px Arial, 'Lato', 'Source Sans Pro', Roboto, Helvetica, sans-serif;
  color: inherit;
  white-space: normal;
  word-break: break-all;
  line-height: 22px;
  margin: 0;
}
.fin-cell-editor .fin-cell-editor-area .textline {
  font-size: 15px;
  font-family: Inconsolata, monospace, arial, sans, sans-serif;
  overflow: hidden;
  visibility: hidden;
  position: fixed;
  top: 0;
  left: 0;
}
.fin-cell-item {
  user-select: none;
  background: 0;
  border: 1px solid transparent;
  outline: none;
  height: 26px;
  color: rgba(0, 0, 0, 0.9);
  line-height: 26px;
  list-style: none;
  padding: 2px 10px;
  cursor: default;
  text-align: left;
}
.fin-cell-item.disabled {
  pointer-events: none;
  opacity: 0.5;
}
.fin-cell-item:hover,
.fin-cell-item.active {
  background: rgba(0, 0, 0, 0.05);
}
.fin-cell-item.divider {
  height: 0;
  padding: 0;
  margin: 5px 0;
  border: none;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}
.fin-cell-item .label {
  float: right;
  opacity: 0.65;
  font-size: 1em;
}
.fin-cell-help-title {
  background-color: #f5f5f5;
  color: #222;
  padding: 1px 0 1px 10px;
  font-family: Inconsolata, monospace, arial, sans, sans-serif;
  font-size: 14px;
  border: 1px solid transparent;
  outline: none;
  height: 26px;
  line-height: 26px;
  list-style: none;
  cursor: default;
  text-align: left;
  user-select: text;
  border-bottom: 1px solid #ebebeb;
}
.fin-cell-help-section-title {
  padding: 1px 0 1px 10px;
  color: #666;
  font-family: Inconsolata, monospace, arial, sans, sans-serif;
  font-size: 11px;
  outline: none;
  height: 26px;
  user-select: text;
  line-height: 26px;
  list-style: none;
  cursor: text;
  text-align: left;
}
.disabled {
  pointer-events: none;
  opacity: 0.5;
}
.fin-cell-help-span2-title {
  font-size: 14px;
}
.fin-cell-help-span-title {
  font-size: 14px;
  background-color: #feb;
}
.fin-cell-help-span3-title {
  background-color: #feb;
}
.fin-cell-help-section-content {
  padding: 1px 0 1px 10px;
  color: #222;
  user-select: text;
  font-family: Inconsolata, monospace, arial, sans, sans-serif;
  font-size: 11px;
  outline: none;
  display: inline-block;
  line-height: 15px;
  list-style: none;
  cursor: text;
  text-align: left;
}
.fin-cell-item.state,
.fin-cell-header.state {
  padding-left: 35px !important;
  position: relative;
}
.fin-cell-item.state:before,
.fin-cell-header.state:before {
  content: '';
  position: absolute;
  width: 10px;
  height: 10px;
  left: 12px;
  top: calc(50% - 5px);
  background: rgba(0, 0, 0, 0.08);
  border-radius: 2px;
}
.fin-cell-item.state.checked:before,
.fin-cell-header.state.checked:before {
  background: #4b89ff;
}
.fin-cell-checkbox {
  position: relative;
  display: inline-block;
  backface-visibility: hidden;
  outline: 0;
  vertical-align: baseline;
  font-style: normal;
  font-size: 1rem;
  line-height: 1em;
}
.fin-cell-checkbox > input {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0 !important;
  outline: 0;
  z-index: -1;
}
.fin-cell-suggest,
.fin-cell-suggest-content,
.fin-cell-contextmenu,
.fin-cell-sort-filter {
  position: absolute;
  box-shadow: 1px 2px 5px 2px rgba(51, 51, 51, 0.15);
  background: #fff;
  z-index: 100;
  width: 260px;
  pointer-events: auto;
  overflow: auto;
}
.fin-cell-suggest {
  width: 200px;
}
.fin-cell-suggest-content {
  width: 200px;
  outline: none;
}
.fin-cell-filter {
  border: 1px solid #e9e9e9;
  font-size: 12px;
  margin: 10px;
}
.fin-cell-filter .fin-cell-header {
  padding: 0.5em 0.75em;
  background: #f8f8f9;
  border-bottom: 1px solid #e9e9e9;
  border-left: 1px solid transparent;
}
.fin-cell-filter .fin-cell-body {
  height: 200px;
  overflow-y: auto;
}
.fin-cell-filter .fin-cell-body .fin-cell-item {
  height: 20px;
  line-height: 20px;
}
.fin-cell-sort-filter .fin-cell-buttons {
  margin: 10px;
}
.fin-cell-toolbar,
.fin-cell-bottombar {
  height: 40px;
  padding: 0 30px;
  text-align: left;
  background: #f5f6f7;
  display: flex;
}
.fin-cell-bottombar {
  border-top: 1px solid #e0e2e4;
}
.fin-cell-toolbar {
  border-bottom: 1px solid #e0e2e4;
}
.fin-cell-toolbar .fin-cell-toolbar-btns {
  display: inline-flex;
}
.fin-cell-toolbar .fin-cell-toolbar-more {
  padding: 0 6px 6px;
  text-align: left;
}
.fin-cell-toolbar .fin-cell-toolbar-more .fin-cell-toolbar-divider {
  margin-top: 0;
}
.fin-cell-toolbar .fin-cell-toolbar-btn {
  flex: 0 0 auto;
  display: inline-block;
  border: 1px solid transparent;
  height: 26px;
  line-height: 26px;
  min-width: 26px;
  margin: 6px 1px 0;
  padding: 0;
  text-align: center;
  border-radius: 2px;
}
.fin-cell-toolbar .fin-cell-toolbar-btn.disabled {
  pointer-events: none;
  opacity: 0.5;
}
.fin-cell-toolbar .fin-cell-toolbar-btn:hover,
.fin-cell-toolbar .fin-cell-toolbar-btn.active {
  background: rgba(0, 0, 0, 0.08);
}
.fin-cell-toolbar-divider {
  display: inline-block;
  border-right: 1px solid #e0e2e4;
  width: 0;
  vertical-align: middle;
  height: 18px;
  margin: 12px 3px 0;
}
.fin-cell-calendar {
  color: rgba(0, 0, 0, 0.65);
  background: #ffffff;
  user-select: none;
}
.fin-cell-calendar .calendar-header {
  font-weight: 700;
  line-height: 30px;
  text-align: center;
  width: 100%;
  float: left;
  background: #f9fafb;
}
.fin-cell-calendar .calendar-header .calendar-header-left {
  padding-left: 5px;
  float: left;
}
.fin-cell-calendar .calendar-header .calendar-header-right {
  float: right;
}
.fin-cell-calendar .calendar-header .calendar-header-right a {
  padding: 3px 0;
  margin-right: 2px;
  border-radius: 2px;
}
.fin-cell-calendar .calendar-header .calendar-header-right a:hover {
  background: rgba(0, 0, 0, 0.08);
}
.fin-cell-calendar .calendar-body {
  border-collapse: collapse;
  border-spacing: 0;
}
.fin-cell-calendar .calendar-body th,
.fin-cell-calendar .calendar-body td {
  width: 14.28571429%;
  min-width: 32px;
  text-align: center;
  font-weight: 700;
  line-height: 30px;
  padding: 0;
}
.fin-cell-calendar .calendar-body td > .cell:hover {
  background: #ecf6fd;
}
.fin-cell-calendar .calendar-body td > .cell.active,
.fin-cell-calendar .calendar-body td > .cell.active:hover {
  background: #ecf6fd;
  color: #2185D0;
}
.fin-cell-calendar .calendar-body td > .cell.disabled {
  pointer-events: none;
  opacity: 0.5;
}
.fin-cell-datepicker {
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2);
  position: absolute;
  left: 0;
  top: calc(100% + 5px);
  z-index: 10;
  width: auto;
}
.fin-cell-buttons {
  display: flex;
  justify-content: flex-end;
}
.fin-cell-buttons .fin-cell-button {
  margin-left: 8px;
}
.fin-cell-button {
  display: inline-block;
  border-radius: 3px;
  line-height: 1em;
  min-height: 1em;
  white-space: nowrap;
  text-align: center;
  cursor: pointer;
  font-size: 1em;
  font-weight: 700;
  padding: 0.75em 1em;
  color: rgba(0, 0, 0, 0.6);
  background: #E0E1E2;
  text-decoration: none;
  font-family: "Lato", "proxima-nova", "Helvetica Neue", Arial, sans-serif;
  outline: none;
  vertical-align: baseline;
  zoom: 1;
  user-select: none;
  transition: all 0.1s linear;
}
.fin-cell-button.active,
.fin-cell-button:hover {
  background-color: #C0C1C2;
  color: rgba(0, 0, 0, 0.8);
}
.fin-cell-button.primary {
  color: #fff;
  background-color: #2185D0;
}
.fin-cell-button.primary:hover,
.fin-cell-button.primary.active {
  color: #fff;
  background-color: #1678c2;
}
.fin-cell-form-input {
  font-size: 1em;
  position: relative;
  font-weight: 400;
  display: inline-flex;
  color: rgba(0, 0, 0, 0.87);
}
.fin-cell-form-input input {
  z-index: 1;
  margin: 0;
  max-width: 100%;
  flex: 1 0 auto;
  outline: 0;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  text-align: left;
  line-height: 30px;
  height: 30px;
  padding: 0 8px;
  background: #fff;
  border: 1px solid #e9e9e9;
  border-radius: 3px;
  transition: box-shadow 0.1s ease, border-color 0.1s ease;
  box-shadow: inset 0 1px 2px hsla(0, 0%, 4%, 0.06);
}
.fin-cell-form-input input:focus {
  border-color: #4b89ff;
  box-shadow: inset 0 1px 2px rgba(75, 137, 255, 0.2);
}
.fin-cell-form-select {
  position: relative;
  display: inline-block;
  background: #fff;
  border: 1px solid #e9e9e9;
  border-radius: 2px;
  cursor: pointer;
  color: rgba(0, 0, 0, 0.87);
  user-select: none;
  box-shadow: inset 0 1px 2px hsla(0, 0%, 4%, 0.06);
}
.fin-cell-form-select .input-text {
  text-overflow: ellipsis;
  white-space: nowrap;
  min-width: 60px;
  width: auto;
  height: 30px;
  line-height: 30px;
  padding: 0 8px;
}
.fin-cell-form-fields {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.fin-cell-form-fields .fin-cell-form-field {
  flex: 0 1 auto;
}
.fin-cell-form-fields .fin-cell-form-field .label {
  display: inline-block;
  margin: 0 10px 0 0;
}
.fin-cell-form-field {
  display: block;
  vertical-align: middle;
  margin-left: 10px;
  margin-bottom: 10px;
}
.fin-cell-form-field:first-child {
  margin-left: 0;
}
.fin-cell-form-field.error .fin-cell-form-select,
.fin-cell-form-field.error input {
  border-color: #f04134;
}
.fin-cell-form-field .tip {
  color: #f04134;
  font-size: 0.9em;
}
.fin-cell-dimmer {
  display: none;
  position: absolute;
  top: 0 !important;
  left: 0 !important;
  width: 100%;
  height: 100%;
  text-align: center;
  vertical-align: middle;
  background-color: rgba(0, 0, 0, 0.6);
  opacity: 0;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation-duration: 0.5s;
  animation-duration: 0.5s;
  transition: background-color 0.5s linear;
  user-select: none;
  z-index: 1000;
}
.fin-cell-dimmer.active {
  display: block;
  opacity: 1;
}
.fin-cell-modal,
.fin-cell-toast {
  font-size: 13px;
  position: fixed;
  z-index: 1001;
  text-align: left;
  line-height: 1.25em;
  min-width: 360px;
  color: rgba(0, 0, 0, 0.87);
  font-family: 'Lato', 'Source Sans Pro', Roboto, Helvetica, Arial, sans-serif;
  border-radius: 4px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  background-color: #fff;
  background-clip: padding-box;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 2px 8px;
}
.fin-cell-toast {
  background-color: rgba(255, 255, 255, 0.85);
}
.fin-cell-modal-header,
.fin-cell-toast-header {
  font-weight: 600;
  background-clip: padding-box;
  background-color: rgba(255, 255, 255, 0.85);
  border-bottom: 1px solid rgba(0, 0, 0, 0.05);
  border-radius: 4px 4px 0 0;
}
.fin-cell-modal-header .fin-cell-icon,
.fin-cell-toast-header .fin-cell-icon {
  position: absolute;
  right: 0.8em;
  top: 0.65em;
  border-radius: 18px;
}
.fin-cell-modal-header .fin-cell-icon:hover,
.fin-cell-toast-header .fin-cell-icon:hover {
  opacity: 1;
  background: rgba(0, 0, 0, 0.08);
}
.fin-cell-toast-header {
  color: #F2711C;
}
.fin-cell-modal-header {
  border-bottom: 1px solid #e0e2e4;
  background: rgba(0, 0, 0, 0.08);
  font-size: 1.0785em;
}
.fin-cell-modal-header,
.fin-cell-modal-content,
.fin-cell-toast-header,
.fin-cell-toast-content {
  padding: 0.75em 1em;
}
@media screen and (min-width: 320px) and (max-width: 480px) {
  .fin-cell-toolbar {
    display: none;
  }
}
