  opacity: 0.1;
}
.fin-cell-revisions-sidebar {
  height: 100%;
  position: absolute;
  box-shadow: -3px 0px 6px -3px rgba(60, 64, 67, 0.15);
  top: 0px;
  background: #fafafa;
  overflow-y: auto;
  z-index: 1;
}
.fin-cell-revisions-sidebar-year {
  padding: 10px 16px;
  font-size: 14px;
}
.fin-cell-revisions-sidebar-date {
  font-size: 14px;
  text-align: center;
  letter-spacing: 1px;
  border-bottom: 1px solid;
  width: 82%;
  margin: 0 auto;
  padding-bottom: 4px;
  padding-top: 8px;
  color: #303133;
}
.fin-cell-revisions-sidebar-title {
  font-size: 14px;
  line-height: 24px;
  padding: 10px 8px;
  height: 20.5px;
  border-bottom: 1px solid #e0e2e4;
}
.fin-cell-revisions-sidebar-title-content {
  margin-left: 10px;
}
.fin-cell-hyperlink-tooltip {
  background: #fcfcfc;
  box-shadow: 0px 0px 2px 0px rgba(0, 0, 0, 0.15), 0px 1px 2px 0px rgba(0, 0, 0, 0.4);
  border-radius: 1px;
  display: inline-block;
  padding: 5px 18px 5px 7px;
  position: absolute;
  z-index: 9999999;
}
.fin-cell-advice-style:hover {
  background: #ddd;
}
.fin-cell-advice-style {
  padding: 5px;
}
.fin-cell-advice {
  z-index: 100000;
  box-shadow: 1px 2px 5px 2px rgba(51, 51, 51, 0.15);
  position: absolute;
  border: 1px solid transparent;
  top: 0;
  pointer-events: auto;
  overflow: auto;
  line-height: 20px;
  left: 0;
  background: white;
}
.check {
  position: relative;
  display: inline-block;
  width: 17px;
  height: 18px;
  border-radius: 25px;
}
.check::after {
  content: "";
  position: absolute;
  left: 5px;
  top: 8px;
  width: 50%;
  height: 25%;
  border: 2px solid #000;
  border-radius: 1px;
  border-top: none;
  border-right: none;
  background: transparent;
  transform: rotate(-45deg);
}
.fin-cell-editor-textEl {
  line-height: 22px;
  background: white;
  caret-color: white;
  left: 2px;
  cursor: default;
  opacity: 0;
  outline: none;
  font-size: 15px;
  margin-left: 2px;
  font-family: Inconsolata, monospace, arial, sans, sans-serif;
}
.fin-cell-paste-overlay-container {
  position: absolute;
  top: 0px;
  width: 100%;
  height: 100%;
  z-index: 10000;
}
.paste-img {
  width: 100%;
  height: 100%;
}
.fin-cell-selector-cell {
  position: absolute;
  border: 2px solid rgba(75, 137, 255);
  background: rgba(75, 137, 255, 0.1);
  cursor: pointer;
}
.border {
  border: #4a98be 1px solid;
  position: absolute;
  left: -7px;
  right: -7px;
  top: -7px;
  bottom: -7px;
  display: none;
  z-index: 99;
}
.active .direction,
.active .border {
  display: block;
}
.direction {
  position: absolute;
  width: 5px;
  height: 5px;
  border: 1px #4a98be solid;
  background-color: #7fd6ff;
  display: none;
  z-index: 100;
}
.line1 {
  width: 100%;
  height: 1px;
  background: #e3e3e3;
}
.line2 {
  position: absolute;
  left: 0px;
  height: 100%;
  width: 1px;
  top: 0px;
  background: #e3e3e3;
}
.line3 {
  background: #e3e3e3;
  position: absolute;
  top: 0px;
  width: 100%;
  height: 1px;
}
.line4 {
  position: absolute;
  right: 0px;
  height: 100%;
  width: 1px;
  top: 0px;
  background: #e3e3e3;
}
.nw {
  left: -2px;
  top: -2px;
  cursor: nw-resize;
}
.w {
  left: -2px;
  top: 50%;
  margin-top: -3px;
  cursor: w-resize;
}
.ws {
  left: -2px;
  bottom: -2px;
  cursor: sw-resize;
}
.s {
  left: 50%;
  bottom: -2px;
  margin-left: -3px;
  cursor: s-resize;
}
.se {
  right: -2px;
  bottom: -2px;
  cursor: se-resize;
}
.e {
  right: -2px;
  top: 50%;
  margin-top: -3px;
  cursor: e-resize;
}
.ne {
  right: -2px;
  top: -2px;
  cursor: ne-resize;
}
.n {
  top: -2px;
  left: 50%;
  margin-left: -3px;
  cursor: n-resize;
}
